html {
  color: var(--gray-700);
  font-family: var(--Roboto);
  font-size: 16px; }

body,
ul,
ol {
  margin: 0;
  padding: 0; }

body {
  position: relative; }

*,
*::before,
*::after {
  box-sizing: border-box;
  font-variant-numeric: lining-nums; }

fieldset {
  border: none; }

input::-moz-placeholder {
  opacity: 1; }

input::-webkit-input-placeholder {
  opacity: 1; }

input:-ms-input-placeholder {
  opacity: 1; }

input::-ms-input-placeholder {
  opacity: 1; }

input::placeholder {
  opacity: 1; }

input[type='number'] {
  -moz-appearance: textfield; }
  input[type='number']::-webkit-outer-spin-button, input[type='number']::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0; }

select {
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none; }

select::-ms-expand {
  display: none; }

h1,
p {
  margin-top: 0; }

@font-face {
  font-family: 'Roboto';
  src: local("Roboto"), local("Roboto-Regular"), url("../assets/fonts/Roboto-Regular.woff2") format("woff2"), url("../assets/fonts/Roboto-Regular.woff") format("woff"), url("../assets/fonts/Roboto-Regular.ttf") format("truetype");
  font-weight: normal;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: 'Roboto';
  src: local("Roboto Bold"), local("Roboto-Bold"), url("../assets/fonts/Roboto-Bold.woff2") format("woff2"), url("../assets/fonts/Roboto-Bold.woff") format("woff"), url("../assets/fonts/Roboto-Bold.ttf") format("truetype");
  font-weight: bold;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: 'Oswald';
  src: local("Oswald Medium"), local("Oswald-Medium"), url("../assets/fonts/Oswald-Medium.woff2") format("woff2"), url("../assets/fonts/Oswald-Medium.woff") format("woff"), url("../assets/fonts/Oswald-Medium.ttf") format("truetype");
  font-weight: 500;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: 'Oswald';
  src: local("Oswald Regular"), local("Oswald-Regular"), url("../assets/fonts/Oswald-Regular.woff2") format("woff2"), url("../assets/fonts/Oswald-Regular.woff") format("woff"), url("../assets/fonts/Oswald-Regular.ttf") format("truetype");
  font-weight: normal;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: 'Oswald';
  src: local("Oswald Light"), local("Oswald-Light"), url("../assets/fonts/Oswald-Light.woff2") format("woff2"), url("../assets/fonts/Oswald-Light.woff") format("woff"), url("../assets/fonts/Oswald-Light.ttf") format("truetype");
  font-weight: 300;
  font-style: normal;
  font-display: swap; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  bottom: 0;
  overflow: hidden;
  clip: rect(0 0 0 0); }

.overflow_hidden {
  overflow: hidden; }

.overflow-y_auto {
  overflow-y: auto; }

.overflow-y_visible {
  overflow-y: visible; }

.overflow-y_hidden {
  overflow-y: hidden; }

.swiper-wrapper > * {
  flex-shrink: 0; }
